import React from "react"
import style from "../styles/experience.module.css"
const Experience = () => {
    return (

        <div className={style.dark}>
            <h3 style={{ fontSize: "30", lineHeight: "2" }}>Experiences</h3>

            <Job
                time="June 2019 – Present"
                title="Full Stack Developer (Fixed-term)"
                company="Motec (China) product family"
                location="London, United Kingdom"
                description1="Designed and developed progressive single page web application, complying with the company's product repairing system.
                            Collaborating with sales team, after-sales team, product maintenance team and research team, 
                            in order to build the app with corresponding authorities and requirements. The app allows staffs to fill in forms 
                            with different level of accessibilities to the form field in real time and avoid file abundance due to transmission"
                description2="Building web application and desktop application based on the self-innovated Fuzzy Matching algorithm, which is 
                            then able to join up different user inputted systems, performing hierarchical Fuzzy Matching, dimension conformity, 
                            geological conformity with very low false-positive rate"
                description3="Designed and implemented a completely cloud-based documentation analysis framework, which is able to perform 
                            textual analysis on large (100,000+) amount of picture-based (.pdf, .png) documents, performing OCR, analysing contents,
                            split / rename / redistribute documents and generate tabular reports from the contents of analysed documents"
                description4="Managing and analysing client propensity model"
                description5="Developing reporting for Product data / IT Infrastructure data / Client data and presenting to the board"
            />
            <Job
                time="March 2018 – Feburary 2019"
                title="Software developer (Fixed-term)"
                company="INSERM (Institut national de la santé et de la recherche médicale)"
                location="Paris, France"
                description1="Implementing the multiplicative algorithm in R / S4 / Visual C++."
                description2="Integrating algorithms into new version of software PFIM."
                description3="Completing the version to release as R package."
                // description4="Publishing the article about the multiplicative algorithm as second author."
            />
            <Job
                time="June 2016 – September 2016"
                title="Actuary (Internship)"
                company="China Export and Credit Insurance Corporation"
                location="Beijing, China"
                description1="Editing quarterly and monthly risk supervisory reports."
                description2="Meeting minutes taking and organizations."
                description3="Creating various VBA utilities for risk monitoring screening processes."
                description4="Collating reserve data and case libraries."
            />

            <h3 style={{ fontSize: "30", lineHeight: "2" }}>Education</h3>
            <Job
                time="2015 – 2018"
                title="Master’s Degree"
                company="Institut National des Sciences Appliquées de Rouen"
                location="France"
                description1="Applied Mathematics and Software Engineering"
            />
            <Job
                time="2013 – 2015"
                title="Grande Ecole Preparatory"
                company="Institut National des Sciences Appliquées de Rouen"
                location="France"
                description1="Science and Technology for Engineer"
            />
            <Job
                time="2010 – 2013"
                title="High School Diploma"
                company="Beijing National Day School"
                location="China"
                description1="Sciences"
            />
        </div>
    )
}

const Job = (props) => (
    <section className={style.container} >
        <div className={style.box} data-sal="slide-up"
            data-sal-delay="100"
            data-sal-easing="ease">
            <p >{props.time}</p>
        </div>
        <p className={style.title} data-sal="slide-up"
            data-sal-delay="100"
            data-sal-easing="ease">{props.title}</p>
        <br></br>
        <div data-sal="slide-up"
            data-sal-delay="100"
            data-sal-easing="ease">
            <p className={style.company}>{props.company}</p>
            <p className={style.location}>{props.location}</p>
        </div>
        <ul style={{ listStyle: 'none', marginLeft: '0' }}>
            <li data-sal="slide-up"
                data-sal-delay="100"
                data-sal-easing="ease">{props.description1}</li>
            <li data-sal="slide-up"
                data-sal-delay="100"
                data-sal-easing="ease">{props.description2}</li>
            <li data-sal="slide-up"
                data-sal-delay="100"
                data-sal-easing="ease">{props.description3}</li>
            <li data-sal="slide-up"
                data-sal-delay="100"
                data-sal-easing="ease">{props.description4}</li>
                <li data-sal="slide-up"
                    data-sal-delay="100"
                    data-sal-easing="ease">{props.description5}</li>
        </ul>
        <hr className={style.line}></hr>
        <br></br>
    </section>
)

export default Experience